import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter } from "react-router";
import "./index.css";
import App from "./App";
//import * as serviceWorker from "./serviceWorker";

export default function () {
  ReactDOM.render(
    <BrowserRouter>
      <App />
    </BrowserRouter>,

    document.getElementById("root")
  );
}
